import React from 'react'

// import React, { useRef, useState } from 'react';
// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';

// import required modules
import { Autoplay,  Navigation } from 'swiper/modules';
import Banner from '../Components/Banner';
import Slide from '../Components/Slide';
import { useNavigate } from 'react-router-dom';

const Home = (props) => {
  let navigate = useNavigate();
  const {newitem, newitem_w} = props;

  return (
    <>
        <Swiper slidesPerView={1}        
        loop={true}
        navigation={true}
        autoplay={{
          delay: 8000,
          disableOnInteraction: false,
        }}      
        modules={[ Autoplay, Navigation ]}
        className="mySwiper oneslide">
          
          <div className='w_logo'>
            <img src={process.env.PUBLIC_URL +"/img/giodano.png"} alt="" />
          </div>

          <div className='icons'>
            <p>로그인</p>
            <p>회원가입</p>
            <p>고객센터</p>
            <img src={process.env.PUBLIC_URL +"/img/icon.png"} alt="" />
            <img src={process.env.PUBLIC_URL +"/img/icon2.png"} alt="" onClick={()=>{navigate('/cart')}}/>
          </div>

          <div className='tag none'>
            <p># NEW LIGHT DOWN</p>
            <p># ALL ABOUT OUTER</p>
            <p># KEEP ME WARM</p>
            <p># Light Weight Padding</p>
          </div>

        <SwiperSlide className='vd_color'>
          
          <video src={process.env.PUBLIC_URL +"/img/video.mp4"} loop muted autoPlay></video>

        </SwiperSlide>
        <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/slide.jpg"} alt="" />
          <div className='event now'>
            <h1>NEW LIGHT DOWN</h1>
            <button>SHOP NOW 〉</button>
          </div>
        </SwiperSlide>
        <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/slide3.jpg"} alt="" />
          <div className='event now'>
            <h1>ALL ABOUT OUTER</h1>
            <button>SHOP NOW 〉</button>
          </div>
        </SwiperSlide>
        <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/slide4.jpg"} alt="" />
          <div className='event3 now'>
            <h1>스웨터의 계절</h1>
            <button>SHOP NOW 〉</button>
          </div>
        </SwiperSlide>
        <SwiperSlide><img src={process.env.PUBLIC_URL +"/img/slide2.jpg"} alt="" />
        <div className='event2 now'>
            <h1>포토카드 증정 이벤트</h1>
            <button>VIEW EVENT</button>
          </div>
        </SwiperSlide>
      </Swiper>

          <Banner></Banner>


          <Slide newitem={newitem} newitem_w={newitem_w}></Slide>
    </>
  )
}

export default Home