import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import { useNavigate } from 'react-router-dom'


let banner={
    width: '100%',
    height: '100%'
}

let Padding = {
    padding: '130px 0px 70px 0',
}


const Banner = () => {
    const navigate = useNavigate();
  return (
    <Container className='banner_div'>
        <Row className='banner_top'>
            <Col className='bn_img' md={6} >
                <img style={banner} src={process.env.PUBLIC_URL +"/img/banner.jpg"} alt="" />
                <p className='list'>NEW ARRIVALS</p>
            </Col>
            <Col className='bn_img' md={6} ><img style={banner} src={process.env.PUBLIC_URL +"/img/banner1.jpg"} alt="" />
                <p className='list1'>BEST</p>
            </Col>
        </Row>
        <Row className='banner_bottom'>
            <Col className='bn_img' md={6} ><img style={banner} src={process.env.PUBLIC_URL +"/img/banner2.jpg"} alt="" />
                <p className='list2'>MEN</p>
            </Col>
            <Col className='bn_img' md={6} ><img style={banner} src={process.env.PUBLIC_URL +"/img/banner3.jpg"} alt="" />
                <p className='list3'>WOMEN</p>
            </Col>
        </Row>


        <Row className='fall' style={Padding}>
                <Col md={8}><img src={process.env.PUBLIC_URL +"/img/banner4.jpg"} style={{ width: '100%', maxWidth: '1100px' }} alt="" /></Col>
                <Col bd={4}>
                    <div>
                        <h6 style={{ color: '#888', fontWeight: '600', fontSize: '14px', padding: '50px 0 0 0'}}>일상을 채우는 새로움</h6>

                        <div className='text_box'>
                            <h2>23 FALL ITEM</h2>
                            <h6>with 한소희, 차은우</h6>
                        </div>

                        <button>SHOP NOW</button>
                    </div>
                </Col>
            </Row>

    </Container>

  )
}

export default Banner
