import React, { useEffect, useState } from 'react'
import './Cart.scss'
import { Table, Row, Col, Container } from 'react-bootstrap'
import { useDispatch, useSelector } from "react-redux";
import { addCount, deleteItem, minusCount, minusItem, sortName } from '../store';

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';

// import required modules
import { Navigation, Autoplay } from 'swiper/modules';
import cart_img from '../Cart_img';

const Cart = () => {

    let [commend, setCommend] = useState(cart_img);

    let [hovers, setHovers] = useState([false,false,false,false,false,false,false,false])

    let cart = useSelector(state=>state.cart);

    let dispatch = useDispatch()

    let [shipping, setShipping] = useState(3000)


    
    const total = () => {
        let sum = 0
        for (let i in cart) {
            sum += cart[i].price * cart[i].amount
        }
        return sum
    }

    useEffect(() => {
        if (total() < 100000) {
            setShipping(3000)
        } else {
            setShipping(0)
        }
    }, [cart])

    return (
        <>

            <Row className='sub_top'>
                <Col md={6}>
                    <p className='category'>Home / 장바구니 </p>
                </Col>
                <Col md={3}>
                    <div className='iconz'>
                        <p>로그인</p>
                        <p>회원가입</p>
                        <p>고객센터</p>
                        <img src={process.env.PUBLIC_URL + "/img/icon_b.png"} alt="" />
                        <img src={process.env.PUBLIC_URL + "/img/icon_b2.png"} alt="" />
                    </div>
                </Col>
            </Row>

            <Container className='cart'>

                <Row className='h3_p'>
                    <h3>장바구니</h3>
                    <p className='ms-3'>※쿠폰은 결제화면에서 적용가능합니다.</p>
                </Row>

                <Table>
                    <thead>
                        <tr>
                            <th style={{ width: "50px" }}><input type="checkbox" name="chek" id="chek" /></th>
                            <th className='prod_name'>상품명</th>
                            <th>가격</th>
                            <th>수량</th>
                            <th>총금액</th>
                            <th style={{ width: "70px" }}>삭제</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            cart.map((v, i) =>
                                <tr key={i}>
                                    <td className='check_btn'><input type="checkbox" name="checkd" id="checkd" /></td>
                                    <td className='produce_'>

                                        <img src={process.env.PUBLIC_URL + v.imgurl} alt="" width='100px' />


                                        <div className='ms-3'>
                                            <h6>{v.title}</h6>
                                            <p>색상 : {v.color} / 사이즈 : {v.size}</p>

                                            <p className='option'>옵션변경</p>
                                        </div>

                                    </td>

                                    <td>
                                        <p>
                                            <span>{v.sale.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</span> <br />
                                            <b>{v.price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")+'원'}</b>
                                        </p>
                                    </td>

                                    <td>
                                        <div className='plus'>
                                            <button onClick={()=>{
                                                dispatch(minusCount(v.id))
                                            }}>－</button>
                                            <p style={{ margin: "0 20px" }}>{v.amount}</p>
                                            <button onClick={()=>{
                                                dispatch(addCount(v.id))
                                            }}>＋</button>
                                        </div>

                                        <p className='change'>수량변경</p>
                                    </td>

                                    <td>
                                        <p>{(v.price * v.amount).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")+'원'}</p>
                                    </td>

                                    <td className='del'><p className='del_btn' onClick={()=>{
                                        dispatch(deleteItem(v.id))
                                    }}>X</p></td>
                                </tr>
                            )
                        }
                        
                       
                    </tbody>
                </Table>

                <div className='btn_two btn2'>
                    <button>선택삭제</button>
                    <button>전체삭제</button>
                </div>

                <div className='payment pm_div'>

                    <div className='pay_div' style={{ maxWidth: '1000px' }}>
                        <div className='pay_p'>
                            <p>총 주문 금액</p>
                            <p><span>{total().toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</span>원</p>
                        </div>
                        <p>＋</p>
                        <div className='pay_p'>
                            <p>배송비</p>
                            <p><span>{shipping.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</span>원</p>
                        </div>
                        <p>=</p>
                        <div className='pay_p'>
                            <p>총 결제 금액</p>
                            <p><b>{(total()+shipping).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</b>원</p>
                        </div>
                    </div>

                    <p>＊주문금액 10만 원 이상부터 무료배송입니다.</p>

                </div>

                <div className='btn_three btn3'>
                    <button>계속 쇼핑하기</button>
                    <button>선택상품 주문하기</button>
                    <button className='bk_btn'>전체상품 주문하기</button>
                </div>

                <div className='npay green_s'>

                    <div className='npay_buy'>
                        <img src={process.env.PUBLIC_URL + "/img/npay.png"} alt="" className='npay_img' />
                        <p className='npay_btn'><img src={process.env.PUBLIC_URL + "/img/npay_btn.png"} alt="" /></p>
                        <p className='npay_btn2'><img src={process.env.PUBLIC_URL + "/img/npay_zzim.png"} alt="" /></p>
                    </div>

                    <Swiper
                        loop={true}
                        autoplay={{
                            delay: 3000,
                            disableOnInteraction: false,
                        }}
                        navigation={true}
                        modules={[Navigation, Autoplay]}
                        className="mySwiper npay_slide">
                        <SwiperSlide><p><span>현장결제</span> 결제할 때 마다, 월 50번 포인트 뽑기!</p></SwiperSlide>
                        <SwiperSlide><p><span>머니결제혜택</span> 최대3%적립 + 소득공제</p></SwiperSlide>
                    </Swiper>

                </div>

                <div className='recommend item_s' >
                    <h4>추천 아이템</h4>

                    <Swiper
                        loop={true}
                        slidesPerView={4}
                        spaceBetween={10}
                        navigation={true}
                        modules={[Navigation]}
                        className="mySwiper commen_slide">

                        {
                            commend.map((v,i)=>
                            <SwiperSlide key={i}
                                onMouseOver={() => {
                                    let copy = [false, false, false, false, false, false, false, false, false];
                                    copy[i] = true;
                                    setHovers(copy)
                                }}
                                onMouseOut={() => {
                                    let copy = [false, false, false, false, false, false, false, false, false];
                                    copy[i] = false;
                                    setHovers(copy)
                                }}>

                                <div className='hover_sd'>
                                    <img src={hovers[i] ? process.env.PUBLIC_URL + v.hoverimg : process.env.PUBLIC_URL + v.imgurl} alt="" />
                                    <div className='privew p_hover'>
                                        <p><img src={process.env.PUBLIC_URL + "/img/icon3.png"} className='priv' alt="" /></p>
                                        <p><img src={process.env.PUBLIC_URL + "/img/icon4.png"} className='priv' alt="" /></p>
                                    </div>

                                    <h6>{v.title}</h6>
                                    <p>{v.price} <span>{v.sale}</span></p>
                                </div>

                            </SwiperSlide>

                            )
                        }
                        {/* <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/cart.jpg"} alt="" /></SwiperSlide>
                        <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/cart1.jpg"} alt="" /></SwiperSlide>
                        <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/cart2.jpg"} alt="" /></SwiperSlide>
                        <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/cart3.jpg"} alt="" /></SwiperSlide>
                        <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/cart4.jpg"} alt="" /></SwiperSlide>
                        <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/cart5.jpg"} alt="" /></SwiperSlide>
                        <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/cart6.jpg"} alt="" /></SwiperSlide>
                        <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/cart7.jpg"} alt="" /></SwiperSlide> */}
                    </Swiper>
                </div>

            </Container>


        </>
    )
}

export default Cart