import React from 'react'
import './Slide.scss'
import { Col, Container, Row } from 'react-bootstrap'

// Import Swiper React components
import 'swiper/css';
import 'swiper/css/navigation';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Navigation } from 'swiper/modules';

import { useState } from 'react';
import { useNavigate } from 'react-router-dom';


const Slide = (props) => {
  let navigate = useNavigate();

  let { newitem, newitem_w } = props;
  let [tab, setTab] = useState(0);
  let [tbclick, setTbclick] = useState([true, false]);
  let [hover, setHover] = useState([false, false, false, false, false, false, false, false, false]);


  return (

    <>

      <Container className='new arrivals'>

        <h2>NEW ARRIVALS</h2>

        <div className='list'>
          <p className={tbclick[0] ? 'tbclick' : null}
            onClick={() => {
              let copy = [false, false]
              copy[0] = true
              setTbclick(copy)
              setTab(0)
            }}>MEN</p>
          <p> / </p>
          <p className={tbclick[1] ? 'tbclick' : null}
            onClick={() => {
              let copy = [false, false]
              copy[1] = true
              setTbclick(copy)
              setTab(1)
            }}>WOMEN</p>
        </div>

        <br />

        <Swiper watchSlidesProgress={true} slidesPerView={4}
          loop={true}
          autoplay={{
            delay: 5000,
            disableOnInteraction: false,
          }}
          navigation={true}
          modules={[Autoplay, Navigation]}
          className="mySwiper slide2">
          {
            tab === 0 ?
              newitem.map((v, i) =>
                <SwiperSlide key={i}
                  onMouseOver={() => {
                    let copy = [false, false, false, false, false, false, false, false, false];
                    copy[i] = true;
                    setHover(copy)
                  }}
                  onMouseOut={() => {
                    let copy = [false, false, false, false, false, false, false, false, false];
                    copy[i] = false;
                    setHover(copy)
                  }}
                  onClick={()=>{navigate(`/detail/${v.id}`)}}>

                  <div className='hover_sd'>
                    <img src={hover[i] ? process.env.PUBLIC_URL + v.hoverimg : process.env.PUBLIC_URL + v.imgurl} alt="" />
                    <div className='privew p_hover'>
                      <p><img src={process.env.PUBLIC_URL + "/img/icon3.png"} className='priv' alt="" /></p>
                      <p><img src={process.env.PUBLIC_URL + "/img/icon4.png"} className='priv' alt="" /></p>
                    </div>

                    <h6>{v.title}</h6>
                    <p>{v.price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}원 <span>{v.sale.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</span></p>
                  </div>
                </SwiperSlide>
              )
              :
              newitem_w.map((v, i) =>
                <SwiperSlide key={i}
                  onMouseOver={() => {
                    let copy = [...hover]
                    copy = [false, false, false, false, false, false, false, false, false];
                    copy[i] = true;
                    setHover(copy)
                  }}
                  onMouseOut={() => {
                    let copy = [...hover]
                    copy = [false, false, false, false, false, false, false, false, false];
                    copy[i] = false;
                    setHover(copy)
                  }}>

                  <div className='hover_sd'>
                    <img src={hover[i] ? process.env.PUBLIC_URL + v.hoverimg : process.env.PUBLIC_URL + v.imgurl} alt="" />
                    <div className='privew'>
                      <p><img src={process.env.PUBLIC_URL + "/img/icon3.png"} className='priv' alt="" /></p>
                      <p><img src={process.env.PUBLIC_URL + "/img/icon4.png"} className='priv' alt="" /></p>
                    </div>

                    <h6>{v.title}</h6>
                    <p>{v.price} <span>{v.sale}</span></p>
                  </div>
                </SwiperSlide>
              )
          }

        </Swiper>


        <Row style={{margin: '110px 0'}}>
          <Col lg={4} className='left_s'>
            <Swiper
              loop={true}
              autoplay={{
                delay: 4000,
                disableOnInteraction: false,
              }}
              navigation={true}
              modules={[Autoplay, Navigation]}
              className="mySwiper left_slide">
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view1.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view2.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view3.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view4.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view5.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view6.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view7.jpg"} alt="" /></SwiperSlide>
            </Swiper>
          </Col>

          <Col lg={8} className='right_s'>

            <div className='top'>
              <h3>23FW Look Book</h3>
              <h1>FALL IN GIORDANO</h1>
            </div>

            <div className='s_title'>
              <h4>MD's Pick</h4>
            </div>

            <br />


            <Swiper
              slidesPerView={3}
              spaceBetween={12}
              loop={true}
              navigation={true}
              autoplay={{
                delay: 4500,
                disableOnInteraction: false,
              }}
              modules={[Autoplay, Navigation]}
              className="mySwiper slide3">

              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view_r.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view_r1.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view_r2.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view_r3.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view_r4.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view_r5.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view_r6.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view_r7.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view_r8.jpg"} alt="" /></SwiperSlide>
              <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/view_r9.jpg"} alt="" /></SwiperSlide>
            </Swiper>
          </Col>
        </Row>

        <div className='insta'>
          <h1>INSTAGRAM</h1>
          <a href="https://www.instagram.com/giordanokr/" target='_blank' name='instar' rel="noopener noreferrer">@GIORDANOKR</a>
        </div>

        <Row className='instag'>
          <Col md={12}>
            <div className='peed'>
              <img src={process.env.PUBLIC_URL + "/img/insta.jpg"} alt="" />
            </div>
            <div className='peed'>
              <img src={process.env.PUBLIC_URL + "/img/insta0.jpg"} alt="" />
            </div>
            <div className='peed'>
              <img src={process.env.PUBLIC_URL + "/img/insta1.jpg"} alt="" />
            </div>
            <div className='peed'>
              <img src={process.env.PUBLIC_URL + "/img/insta2.jpg"} alt="" />
            </div>
            <div className='peed'>
              <img src={process.env.PUBLIC_URL + "/img/insta3.jpg"} alt="" />
            </div>
          </Col>

          <Col md={12}>
            <div className='peed1'>
              <img src={process.env.PUBLIC_URL + "/img/insta4.jpg"} alt="" />
            </div>
            <div className='peed1'>
              <img src={process.env.PUBLIC_URL + "/img/insta5.jpg"} alt="" />
            </div>
            <div className='peed1'>
              <img src={process.env.PUBLIC_URL + "/img/insta6.jpg"} alt="" />
            </div>
            <div className='peed1'>
              <img src={process.env.PUBLIC_URL + "/img/insta7.jpg"} alt="" />
            </div>
            <div className='peed1'>
              <img src={process.env.PUBLIC_URL + "/img/insta8.jpg"} alt="" />
            </div>
          </Col>

        </Row>


      </Container>
    </>
  )
}

export default Slide